<template>
    <v-container v-if="chatID" class="chat-screen" style=" min-width: 305px !important;">
        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
        >
            <v-row>
                <v-col class="text-left pa-0">
                    <v-btn text icon color="primary"
                           tile
                           height="100%"
                           width="80px"
                           @click="$router.go(-1)">

                        <v-icon>mdi-arrow-left</v-icon>
                        <v-avatar v-if="userProfileImage" style="height: 50px; !important;">
                            <img :src="userProfileImage" alt="Avatar" :aspect-ratio="1"
                                 style=" object-position: center; object-fit: cover;">
                        </v-avatar>
                    </v-btn>
                </v-col>
                <v-col class="text-center">
                    <v-btn tile style="display: inline; top: 50%;
                    -ms-transform: translateY(-50%);
                    transform: translateY(-50%);"
                           text color="primary"
                           @click="$router.push({path: '/dashboard/chats/user/' + userID})">
                        <v-toolbar-title style="font-size: 14px">{{username}}</v-toolbar-title>
                    </v-btn>
                </v-col>
                <v-col class="text-right" style="padding-top: 0; padding-bottom: 0"></v-col>
            </v-row>

        </v-app-bar>

        <div class="conversation">
            <div class="conversation-container">


                <div v-for="message in messages" v-bind:key="message.id"
                     v-bind:class="{'message sent': myUID == message.sender, 'message received': myUID != message.sender}"
                     @change="setMessagesToRead">
                    {{ message.text}}
                    <span class="metadata">
                      <span class="time"></span>
                        <span class="tick"><svg v-if="myUID == message.sender"
                                                xmlns="http://www.w3.org/2000/svg" width="16"
                                                height="15" id="msg-dblcheck-ack" x="2063"
                                                y="2076"><path
                                d="M15.01 3.316l-.478-.372a.365.365 0 0 0-.51.063L8.666 9.88a.32.32 0 0 1-.484.032l-.358-.325a.32.32 0 0 0-.484.032l-.378.48a.418.418 0 0 0 .036.54l1.32 1.267a.32.32 0 0 0 .484-.034l6.272-8.048a.366.366 0 0 0-.064-.512zm-4.1 0l-.478-.372a.365.365 0 0 0-.51.063L4.566 9.88a.32.32 0 0 1-.484.032L1.892 7.77a.366.366 0 0 0-.516.005l-.423.433a.364.364 0 0 0 .006.514l3.255 3.185a.32.32 0 0 0 .484-.033l6.272-8.048a.365.365 0 0 0-.063-.51z"
                                :fill="[message.read === true ? '#006400' : '#a9a9a9']"/></svg></span>
                  </span>
                </div>
            </div>
            <v-form>
                <div class="message-sender">
                    <v-text-field
                            v-model="textMessageContent"
                            solo
                            dense
                            rounded
                            style="margin-right: 20px"
                    ></v-text-field>
                    <v-btn color="primary" @click="sendMessage" :disabled="!textMessageContent">
                        <v-icon>mdi-send</v-icon>
                    </v-btn>
                </div>
            </v-form>
        </div>
        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import {mapActions, mapMutations, mapState} from 'vuex';
    import Constants from "../../store/Constants";

    export default {
        name: "ChatWithUser",
        data() {
            return {
                userProfileImage: null,
                textMessageContent: '',
                username: null,
                myUID: this.$store.getters.getUserInfoUID,
                snackbar: false,
                snackbarText: '',
                myActiveChat: null,
                closeText: Constants.TEXT_CLOSE,
                chatID: null,
                userID: null
            }
        },
        methods: {
            ...mapActions({
                closeChatWithUserFirebase: 'closeChatWithUserFirebase',
                setChatWithUserMessagesToRead: 'setChatWithUserMessagesToRead',
                sendChatMessageFirebase: 'sendChatMessageFirebase',
                resetActiveChat: 'resetActiveChat',
                prepareChatWithUser:'prepareChatWithUser'
            }),
            ...mapMutations([
                'setChatsInfoActiveChat'
            ]),
            setMessagesToRead: function () {
                this.setChatWithUserMessagesToRead(this.chatID);
            },
            sendMessage: function () {
                let self = this;

                let resultSendChatMessageFirebase = self.sendChatMessageFirebase({
                    'chatID': self.chatID,
                    'messageText': self.textMessageContent,
                });

                resultSendChatMessageFirebase
                    .then(() => {
                        self.textMessageContent = '';
                        /*let conv = self.$el.querySelector(".conversation");
                        conv.scrollTop = conv.scrollHeight;*/

                    })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },
            prepareChatData: function (chatID) {
                let user = this.$store.getters.getChatUserByChatID(chatID);
                this.userProfileImage = user.profileImage;
                this.username = user.username;
                this.userID = user.id;

                this.$store.commit('setChatsInfoActiveChat', chatID);
                this.setMessagesToRead();
            }
        },
        mounted() {
            let self = this;
            self.chatID = this.$route.params.id;

            if(null == self.$store.getters.getChatWithUserByChatID(self.chatID)){
                let userID = self.chatID.replace(this.$store.getters.getUserInfoUID, "");
                console.log(userID)
                let resultPrepareChatWithUser = self.prepareChatWithUser({
                    chatID: self.chatID,
                    userID: userID
                });

                resultPrepareChatWithUser
                    .then(() => {
                        self.prepareChatData(self.chatID);
                    })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });

            }
            else{
                self.prepareChatData(self.chatID);
            }

        },
        computed: {
            ...mapState({
                chats: state => state.chats,
            }),
            messages: function () {
                let self = this;

                let chat = this.$store.state.chats.find(chat => {
                    return chat.id === self.chatID;
                });

                if(null == chat){
                    return null;
                }
                else{
                    return chat.messages;
                }
            }
        },
        destroyed() {
            this.$store.commit('resetChatsInfoActiveChat');
        },
    }
</script>

<style scoped>
    html {
        width: 100vw;
        width: 100%;
    }

    body {
        width: 100vw;
        height: 100%;
    }

    .chat-screen {
        width: 100vw;
        padding: 0px;
        margin: 0px;
        height: 100vh;
    }

    /* Conversation */
    .conversation {
        height: calc(100% - 56px);
        margin-top: 58px;
        width: 100vw;
        position: relative;
        background: url("https://i.ibb.co/3s1f9Jq/default-wallpaper.png") repeat;
        z-index: 0;
    }

    .conversation ::-webkit-scrollbar {
        transition: all .5s;
        width: 5px;
        height: 1px;
        z-index: 10;
    }

    .conversation ::-webkit-scrollbar-track {
        background: transparent;
    }

    .conversation ::-webkit-scrollbar-thumb {
        background: #b3ada7;
    }

    .conversation .conversation-container {
        height: calc(100% - 56px);
        width: calc(100% - 20px);
        overflow-x: hidden;
        padding: 0 16px;
        margin-bottom: 8px;
    }

    .conversation .conversation-container:after {
        content: "";
        display: table;
        clear: both;
    }

    /* Messages */

    .message {
        color: #000;
        clear: both;
        line-height: 18px;
        font-size: 15px;
        padding: 8px;
        position: relative;
        margin: 8px 0;
        max-width: 85%;
        word-wrap: break-word;
        z-index: -1;
    }

    .message:after {
        position: absolute;
        content: "";
        width: 0;
        height: 0;
        border-style: solid;
    }

    .metadata {
        display: inline-block;
        float: right;
        padding: 0 0 0 7px;
        position: relative;
        bottom: -4px;
    }

    .metadata .tick {
        display: inline-block;
        margin-left: 2px;
        position: relative;
        top: 4px;
        height: 16px;
        width: 16px;
    }

    .metadata .tick svg {
        position: absolute;
        transition: .5s ease-in-out;
    }

    .metadata .tick svg:first-child {
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
        -webkit-transform: perspective(800px) rotateY(180deg);
        transform: perspective(800px) rotateY(180deg);
    }

    .metadata .tick svg:last-child {
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
        -webkit-transform: perspective(800px) rotateY(0deg);
        transform: perspective(800px) rotateY(0deg);
    }

    .metadata .tick-animation svg:first-child {
        -webkit-transform: perspective(800px) rotateY(0);
        transform: perspective(800px) rotateY(0);
    }

    .metadata .tick-animation svg:last-child {
        -webkit-transform: perspective(800px) rotateY(-179.9deg);
        transform: perspective(800px) rotateY(-179.9deg);
    }

    .message:first-child {
        margin: 16px 0 8px;
    }

    .message.received {
        background: #fff;
        border-radius: 0px 5px 5px 5px;
        float: left;
    }

    .message.received .metadata {
        padding: 0 0 0 16px;
    }

    .message.received:after {
        border-width: 0px 10px 10px 0;
        border-color: transparent #fff transparent transparent;
        top: 0;
        left: -10px;
    }

    .message.sent {
        background: lightskyblue;
        border-radius: 5px 0px 5px 5px;
        float: right;
    }

    .message.sent:after {
        border-width: 0px 0 10px 10px;
        border-color: transparent transparent transparent lightskyblue;
        top: 0;
        right: -10px;
    }

    .message-sender {
        display: flex;
        width: calc(100% - 10px);
        padding: 0 10px;
    }

</style>
